* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 -10px;
  display: flex;
}

.search-form {
  background-color: #6ec8aa;
  border-radius: 10px;
  justify-content: center;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px;
  flex: 1;
  padding: 10px;
  font-size: 16px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, .1);
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  padding: 10px;
  font-size: 16px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.search-form button:hover {
  background-color: #3e8e41;
}

.photo-card {
  width: 200px;
  border-radius: 10px;
  flex-basis: calc(25% - 20px);
  margin: 10px;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  display: block;
}

.photo-card .info {
  background-color: #f5f5f5;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.photo-card .info-item {
  justify-content: center;
  align-items: center;
  margin: 0;
  font-size: 14px;
  display: flex;
}

.photo-card .info-item b {
  margin-right: 5px;
  font-weight: bold;
}

.load-more {
  color: #074d30;
  opacity: .5;
  text-align: center;
  width: 150px;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #a23b3b;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px 20px;
  transition: opacity .3s;
  display: block;
}

button:disabled {
  cursor: not-allowed;
  color: #000503;
  background-color: #c1c1c1;
  border: none;
}

button.hidden {
  display: none;
}

.load-more:hover {
  opacity: .8;
}

